.c-row{
	display: flex;
  	flex-wrap: wrap;
	margin-left: -15px;
	margin-right: -15px;
}

@for $i from 1 through 100 {
	.c-col-#{$i} {
	  width: percentage($i / 100);
	  padding-left: 15px;
	  padding-right: 15px;
	}
}

@media #{$vpdesktop}{
	@for $i from 1 through 100 {
		.c-col-md-#{$i} {
		  	width: percentage($i / 100);
	  		padding-left: 15px;
	  		padding-right: 15px;
		}
	}
}
@media (max-width: 991px){
	@for $i from 1 through 100 {
		.c-col-sm-#{$i} {
		  	width: percentage($i / 100);
	  		padding-left: 15px;
	  		padding-right: 15px;
		}
	}
}
@media (max-width: 767px){
	@for $i from 1 through 100 {
		.c-col-xs-#{$i} {
			width: percentage($i / 100);
			padding-left: 15px;
	  		padding-right: 15px;
		}
	}
}

.toSvg{
	width: 100%;
	height: 100%;
	&.svgPrimary{
		polygon,
		path{
			fill: $primary !important;
			stroke: none !important;
		}
	}
	&.svgSecondary{
		polygon,
		path{
			fill: $secondary !important;
			stroke: none !important;
		}
	}
	&.svgWhite{
		polygon,
		path{
			fill:$white !important;
			stroke: none !important;
		}
	}
	&.svgBlack{
		polygon,
		path{
			fill: $black !important;
			stroke: none !important;
		}
	}
}


.c-btn-white,
.c-btn-glow{
	//min-width:200px;
	display: inline-block;
	padding: 16px 25px;
	border-radius: 999px;
	text-align: center;
	color: #fff;
	text-transform: uppercase;
	font-size: 20px; 
	line-height: 1em;
	overflow: hidden;
	position: relative;

	@media (max-width: 1800px){ 
		font-size: 18px; 
		padding: 15px 30px;
	}
	@media (max-width: 1023px){ 
	    font-size: 16px;
    	padding: 14px 24px;
	}
	@media (max-width: 767px){ 
		font-size: 14px;
	}

	&.btn--sm{
		font-size: 16px;
		padding: 15px 30px;
		@media (max-width: 767px){
			font-size: 14px;
			padding: 12px 25px;
		}
		@media (max-width: 360px){
			font-size: 13px;
			line-height: 14px;
		}
	}
}

.c-btn-outline{ 
	display: inline-block;
	padding: 17px 30px;
	border-radius: 17px; 
	text-align: center; 
	color: #9e9e9e;
	text-transform: uppercase;
	font-size: 17px; 
	line-height: 1em; 
	position: relative; 
	border: 1px solid #909090;
	position: relative;

	&:after{
		content: " ";
		width: 100%;
		height: 100%;
		position: absolute;
		border-radius: 22px; 
		left: 0;
		top: 0;
		z-index: -1;
		transition: all 0.5s ease;
		display: none;
		
		}
	&:hover{
	color: #909090;
	&:after{ 
	transition: all 0.5s ease;
		width: 110%;
		height: 130%;
		left: -5%;
		top: -15%;
		opacity: 0;
		border: 3px solid #909090;
		}
		
	}
	@media (max-width: 1800px){ 
		font-size: 18px; 
		padding: 15px 30px;
	}
	@media (max-width: 1023px){ 
	    font-size: 16px;
    	padding: 14px 24px;
	}

	&.btn--sm{
		font-size: 14px;
		padding: 15px 30px 14px;
		@media (max-width: 767px){
			font-size: 13px;
			padding: 12px 25px;
		}
	}
}
.c-btn-glow{
		/* Permalink - use to edit and share this gradient: http://colorzilla.com/gradient-editor/#552879+0,311758+25,170a3f+100 */
background: #552879; /* Old browsers */
background: -moz-linear-gradient(-45deg, #552879 0%, #311758 25%, #170a3f 100%); /* FF3.6-15 */
background: -webkit-linear-gradient(-45deg, #552879 0%,#311758 25%,#170a3f 100%); /* Chrome10-25,Safari5.1-6 */
background: linear-gradient(135deg, #552879 0%,#311758 25%,#170a3f 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#552879', endColorstr='#170a3f',GradientType=1 ); /* IE6-9 fallback on horizontal gradient */
	transition: none;
	&:after{
		content: '';
		display: block;
		width: 100%;
		height: 100%;
		background: linear-gradient(to bottom, #d8aff6 0%,#6d217f 50%,#22063c 100%) center 100% / 100% 150%;
		opacity: 0;
		transition: all .35s ease-in-out;
		position: absolute;
		top: 0;
		left: 0;
		mix-blend-mode: color-dodge;
		z-index: 1;
	}

	span{
		position: relative;
		z-index: 2;
	}

	&:hover{
		&:after{
			opacity: 0.5;
		}
	}
	&:active{
		box-shadow: 2.5px p0x 5px 0px rgba(0, 0, 0, 0.3), inset 0px -4px 9px 1px rgba(226, 190, 255, 0.15);
		background: #521766;
		&:hover{
			&:after{
				display: none;
				transition: none;
			}
		}
	}
	
	@media (max-width: 767px){
		&:after{
			display: none;
		}
		&.btn--m-change-to-green{
			background: #000;
			background: linear-gradient(to right, #529297 0%,#0a676f 30%,#203250 100%) no-repeat center center / 100% 100%;
			box-shadow: none;
			&:active{
				background: #164a5f;
			}
		}
	}
}

.c-btn-white{
	background: rgba(255,255,255,0.15);
	box-shadow: 2.457px 1.721px 5px 0px rgba(0, 0, 0, 0.1);

	&:hover{
		background: rgba(255,255,255,0.3);
	}

	&:active{
		background: rgba(255,255,255,0.1);
		transition: none;
		&:hover{
			transition: none;
		}
	}

	&.btn--txt-black{
		color: #222222;
		background: rgba(255,255,255,0.3);
		&:hover{
			background: rgba(255,255,255,0.5);
		}
		&.btn--play{
			&:before{
				background-image: url(../img/icons/play-black.svg);
			}
		}
	}
	
	&.btn--play-1{
		background: rgba(248,249,249,0.60);
		color: #222222;
		box-shadow: none;
		width: auto;
		min-width: 0;
		padding: 16px 25px 13px;
		transition: all .35s;
		width: 50px;

		.icon{
			display: inline-block;
			width: 19px;
			height: 22px;
			background: url(../img/icons/play.png) no-repeat center center;  
			padding-left: 0;
			transition: all .35s;
			margin: -1px -13px -1px -10px;
		}

		span{
			overflow: hidden;
			width: 0;
			transition: width .35s;
			display: inline-block;
			white-space: nowrap;
		}

		&:hover{
			background: rgba(248,249,249,0.86);

			@media (min-width: 1025px){
				span{
					width: 125px;
				}
				.icon{
					// margin: -1px -13px -1px -10px;
					opacity: 0;
					transform: translateX(160px);
				}
			}
		}
		@media (max-width: 1800px){
			width: 48px;
			padding: 14px 21px 11px;
		}
		@media (max-width: 1023px){
			width: 44px;
			padding: 11px 21px 10px;
		}
	}

	&.btn--play{
		// play.svg
		&:before{
			content: '';
			display: inline-block;
			width: 22px;
			height: 27px;
			background: url(../img/icons/play.svg) no-repeat 50%/100% auto;
			padding-left: 0;
			margin-right: 15px;
			margin-bottom: -6px;
			margin-top: -6px;
		}
		
		&.btn--sm{
			@media (max-width: 767px){
				&:before{
					width: 16px;
					height: 19px;
					margin-top: -5px;
					margin-right: 8px;
				}
			}
		}
	}
}
